import {useState, useRef} from 'react';
import data from "../db/data.json";

const MainBanner = (props) => {
    const sFinRef = useRef();
    const sHillRef = useRef();

    const containerRef = useRef();
    const areaRef = useRef();
    const infoRef = useRef();
    

    const splashRef = useRef();

    const [mapChange, setMapChange] = useState("tab1");

    const [expanding, setExpanding] = useState("");

    const [currentTab, setCurrentTab] = useState();
    const selectMenuHandler = (index) => {setCurrentTab(index);}

    const [imageChange, setImageChange] = useState(0);
    const imageChangeHandler = (index) => {setImageChange(index);}

    const [stretching, setStretching] = useState(true);
    const stretchingHandler = (index) => {setStretching(index);}

    const resetTransition = () => {
        containerRef.current.style.transition = "none";
    }
    const addTransition = () => {
        containerRef.current.style.transition = "1s ease-in-out";
    }

    const [pos, setPos] = useState({ x: 0, y: 0, scale: 1 });
    
    const posDefault = () => {
        pos.x = 0;
        pos.y = 0;
        pos.scale = 1;
        containerRef.current.style.top = "0px";
        containerRef.current.style.left = "0px";
    }

    const onScroll = (e) => {
        const delta = e.deltaY * -0.001;
        const newScale = pos.scale + delta;

        const ratio = 1 - newScale / pos.scale;

        if (pos.scale < 1) {
            posDefault();    
            addTransition();
        } else {
            setPos({
                x: pos.x + (e.clientX - pos.x) * ratio,
                y: pos.y + (e.clientY - pos.y) * ratio,
                scale: newScale
            });
        };
        resetTransition();
    }

    let posX = 0,
        posY = 0;

    const dragStart = e => {
        const img = new Image();
        e.dataTransfer.setDragImage(img, 0, 0);
        
        posX = e.clientX;
        posY = e.clientY;
    }

    const drag = e => {
        e.target.style.left = `${e.target.offsetLeft + (e.clientX - posX)}px`;
        e.target.style.top = `${e.target.offsetTop + (e.clientY - posY)}px`;

        posX = e.clientX;
        posY = e.clientY;
    }

    const dragEnd = e => {
        e.target.style.left = `${e.target.offsetLeft + (e.clientX - posX)}px`;
        e.target.style.top = `${e.target.offsetTop + (e.clientY - posY)}px`;
        
        posX = e.clientX;
        posY = e.clientY;
    }

    const focusSpot = e => {
        containerRef.current.style.top = "0px";
                containerRef.current.style.left = "0px";

                pos.x = 
                    (((areaRef.current.offsetLeft * -1) - e.target.parentNode.offsetLeft) * 2) + (containerRef.current.offsetWidth / 4 - e.target.offsetWidth);

                pos.y = 
                    (((areaRef.current.offsetTop * -1) - e.target.parentNode.offsetTop) * 2) + (containerRef.current.offsetHeight / 2 - e.target.offsetHeight);
                    pos.scale = 2;
    }

    // const wheelHandler = e => { setRatio(ratio => (ratio >= 0.1 ? ratio + 0.001 * e.deltaY : 0.2));}


    const facilities = data.sFinList.map((list, index) => 
        <div className={currentTab === index ? " facility facility-selected" : "facility"}
            onClick={(e)=> {selectMenuHandler(index);}}>
            <div className="hit" onClick={(e) => {
                    addTransition();
                    setImageChange(0);
                    focusSpot(e);}}></div>
            <div className="pin">
                <div className="pin-head">
                    <div className="pin-point"></div>
                    <p className="pin-name">{list.name}</p>
                </div>
                <div className="pin-body"></div>
            </div>
        </div>
    );


    return (
        <div className="map-wrapper" onLoad={() => {
            splashRef.current.classList.add("disappear");
        }}>
            <div className="splash" ref={splashRef}>
                <h2 className="copy-heading">
                    SONO.FIN.VIVLADI <br/> &amp; <br/> SONO.HILLS
                </h2>
            </div>

            <div className="map-container"
                style={{transformOrigin : "0 0",
                        transform : `translate(${pos.x}px, ${pos.y}px) scale(${pos.scale})`}}
                onWheel = {(e) => {onScroll(e); selectMenuHandler();}}
                onDragStart={(e) => {resetTransition(); dragStart(e);}}
                onDrag={drag}
                onDragEnd={dragEnd}
                onDragOver={(e) => {
                    e.preventDefault();
                }}
                ref={containerRef}
                draggable>
                <div className="s-fin" ref={sFinRef}>
                    <div className="s-fin-list" ref={areaRef}>
                        {facilities}
                    </div>
                    <img className="bg-sono-fin" src={data.generalImg[0].bgSono} alt="sono fin map"/>
                </div>

                <div className="s-hill" ref={sHillRef}>
                    <img className="bg-sono-hill" src={data.generalImg[0].bgHill} alt="sono fin map"/>
                </div>
            </div>

            <div className={expanding === "expand" ? "map-swiper hide-swiper" : "map-swiper"}>
                <p className={mapChange === "tab1" ? "swiper active" : "swiper"}
                onClick={(e)=>{
                    setMapChange("tab1");
                    selectMenuHandler();
                    pos.x = 0;
                    pos.y = 0;
                    pos.scale = 1;
                    sFinRef.current.style.opacity = "1";
                    sHillRef.current.style.opacity = "0";
                }}>
                    SONO FIN VIVALDI
                    <i className={mapChange === "tab1" ? "marker active" : "marker"}></i>
                </p>
                <p className={mapChange === "tab2" ? "swiper active" : "swiper"}
                onClick={(e)=>{
                    setMapChange("tab2");
                    selectMenuHandler();
                    pos.x = 0;
                    pos.y = 0;
                    pos.scale = 1;
                    sFinRef.current.style.opacity = "0";
                    sHillRef.current.style.opacity = "1";
                }}>
                    SONO HILLS
                    <i className={mapChange === "tab2" ? "marker active" : "marker"}></i>
                </p>
            </div>

            <div className="area-info">
                { data.sFinList.map((list, index) => 
                    <div className = {currentTab === index ? "info info-showing" : "info"} ref={infoRef}>
                        <div className={expanding === "expand" ? "expander reduction" : "expander"}
                            onClick={(e)=> {
                                if (!e.target.parentNode.classList.contains("info-expanding")) {
                                    e.target.parentNode.classList.add("info-expanding");
                                    setExpanding("expand");
                                    console.log(expanding);
                                } else {
                                    e.target.parentNode.classList.remove("info-expanding");
                                    setExpanding();
                                    console.log(expanding);
                                }
                            }}>
                            <span className="bar"></span>
                            <span className="bar"></span>
                            <span className="bar"></span>
                            <span className="bar"></span>
                            <span className="bar"></span>
                            <span className="bar"></span>
                            <span className="bar"></span>
                            <span className="bar"></span>
                            <span className="bar"></span>
                        </div>

                        <div className="close" onClick={(e)=>{
                                e.target.parentNode.classList.remove("info-expanding");
                                setTimeout(() => {
                                    setPos({ x: 0, y : 0, scale : 1 });
                                    containerRef.current.style.left = "0px";
                                    containerRef.current.style.top = "0px";
                                }, 300);
                                selectMenuHandler();
                                addTransition();
                                setImageChange(0);
                                setStretching();
                                setExpanding();}}>
                            <span className="bar"></span>
                            <span className="bar"></span>
                        </div>
                        
                        <div className="info-content">
                            <div className="info-header-section">
                                <h2 className="copy-heading">{list.name}</h2>
                                <img className="info-illust" src={list.illust} alt={list.name}/>
                            </div>

                            <div className="info-summary">
                                <p className="copy-body">
                                    {list.info}
                                </p>
                            </div>

                            <div className="info-gallery">
                                <h3 className="copy-sub-heading">
                                    Gallery
                                </h3>
                                <ul className="info-image-list">
                                    {list.infoImages?.map((item, index) => 
                                        <li className={imageChange === index ? "selected" : "none"}
                                        onClick={()=> {imageChangeHandler(index)}}>
                                            {item.title}
                                        </li>)}
                                </ul>

                                <div className="info-image-wrap">
                                    {list.infoImages?.map((item, index) => 
                                        <img className={imageChange === index ? "image-front" : "image-back"} src={item.img} alt="image" />
                                    )}
                                </div>
                            </div>

                            <div className="info-spec">
                                <h3 className="copy-sub-heading">
                                    Specifications
                                </h3>
                                <div className="info-spec-wrap">
                                    {list.spec?.map(item => 
                                        <div className="info-spec-block">
                                            <p>{item.title}</p>
                                            <span>{item.content}</span>
                                        </div>
                                    )}
                                </div>
                            </div>

                            <div className="info-fac-service">
                                <h3 className="copy-sub-heading">
                                    Facilities &amp; Service
                                </h3>
                                {list.facility?.map((item, index) => 
                                    <div className="info-fac-service-wrap">
                                        <div className="info-fac-service-service">
                                            <div className="info-fac-service-service-heading">
                                                <h4>
                                                    <span>
                                                        {item.number}
                                                    </span>
                                                    {item.name}
                                                </h4>
                                                <i className={stretching === index ? "stretch-button toggle" : "stretch-button"} onClick={() => { stretching === index ? stretchingHandler() : stretchingHandler(index) }}>
                                                    <span></span>
                                                    <span></span>
                                                </i>
                                            </div>

                                            <div className={stretching === index ? "info-fac-service-content stretched" : "info-fac-service-content"}>
                                                <div className="info-fac-service-image">
                                                    <img src={item.img} />
                                                </div>

                                                {item.services?.map(service => 
                                                    <div className="info-service-desc">
                                                        <p className="info-service-name">
                                                            {service.name}
                                                        </p>
                                                        <p className="info-service-desc">
                                                            {service.content}
                                                        </p>
                                                    </div>
                                                )}
                                            </div>
                                            <hr/>
                                        </div>
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    )
}


export default MainBanner;